one}.button,.form-input{width:100%;background-color:#FCFDFF;border:1px solid #CDD7E5;border-radius:4px}.form__error-info{position:relative;top:-20px;font-family:Inter,sans-serif;font-weight:400;font-size:12px;color:#E1485B;text-align:right}.error{background:#FFFBFB;border:1px solid #E1485B;outline:none}.success{border:1px solid #B0BACA}.form-text{display:block;margin:26px 0;text-align:left;cursor:pointer;color:#2B3151;position:relative;padding-left:30px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.form-text input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.checkmark{position:absolute;top:0;left:0;height:23px;width:23px;border:2px solid #B0BACA;border-radius:3px}.form-text input:checked ~ .checkmark{background-color:#3EBF8A;border:none}.checkmark:after{content:"";position:absolute;display:none}.form-text input:checked ~ .checkmark:after{display:block}.form-text .checkmark:after{left:8px;top:4px;width:5px;height:9px;border:solid white;border-width:0 3px 3px 0;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.form-submit{height:48px;width:100%;font-family:Inter,sans-serif;font-weight:500;font-size:18px;color:#ffffff;background-color:#3EBF8A;border-radius:3px;border:none;cursor:pointer;transition-property:background-color;transition-duration:.5s}.form-submit:hover{background-color:#008960}.successInfo{width:600px;margin:20px auto;padding:50px;border:2px solid #008960}

